import React from "react";
import renderAst from "./renderAst";
import PageFooter from "./components/Footer";
import AffiliateInfo from "./components/AffiliateInfo";
import {H1} from "./components/Header";
import Img from "gatsby-image";

import "./styles.scss";

export const MainSectionWrapper = (props) => {
  const {cover, name, showTitle} = props;
  return (
    <>
      <main className="MainSection">
        <div className="MainSectionInner">

          <div className="MainSectionTitleAndCover">
            {showTitle &&
              <H1>{name}</H1>
            }
            {cover &&
              <div className="HomeCardCoverImage">
                  <Img 
                      fluid={cover.image.childImageSharp.fluid} 
                      title={cover.title} 
                      alt={cover.alt} 
                  />
              </div>
            }
          </div>

          {props.children}
        </div>
      </main>

      {props.affiliate &&
        <AffiliateInfo />
      }

      <PageFooter />
    </>
  );
}

const Stream = (props) => {
  if (!props.data.page) {
    return (
      <>
        <main className="MainSection"></main>
        <PageFooter />
      </>
    );
  }
  const { childMarkdownRemark } = props.data.page;
  const { htmlAst } = childMarkdownRemark;

  return (
    <MainSectionWrapper {...props.data.page}>
      {renderAst(htmlAst)}
    </MainSectionWrapper>
  );
}

export default Stream 